.form,
.form__input,
.form__textarea-text,
.form__button {
  font-family: $font-heading;
  font-weight: 400;
  text-transform: uppercase;
  color: $color-text;
  font-size: 16px;
  line-height: 18px;
}

.form__wrapper {
  display: grid;
  grid-template-columns: 1fr;
}

.form__fieldset--info,
.form__fieldset--target {
  display: inline;
}

.form__fieldset--info {
  padding: 0 20px;
  border: none;
  margin-bottom: 25px;
}

.form__fieldset--info .form__items {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 45px 45px 45px;
  row-gap: 14px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.form__fieldset--info .form__item {
  display: flex;
  align-items: center;
}

.form__fieldset--info .form__label {
  padding-right: 13px;
}

.form__fieldset--info .form__input {
  padding: 0 13px;
  align-self: stretch;
  flex-grow: 1;
  border: 1px solid $color-line-gray;
}

.form__fieldset--target {
  padding: 0 20px;
  margin-bottom: 26px;
  border: none;
  border-top: 1px solid $color-line-gray;
  border-bottom: 1px solid $color-line-gray;
}

.form__fieldset--target .form__items {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin: 0;
  list-style-type: none;
  padding: 22px 0 19px;
}

.form__label-radio {
  position: relative;
  padding-left: 40px;
}

.form__fieldset--target label::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border: 1px solid $color-line-gray;
  border-radius: 50%;
}

.form__fieldset--target input:checked + label::after {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 13px;
  height: 13px;
  top: 50%;
  left: 9px;
  transform: translateY(-50%);
  background-color: $color-bg-green;
  border-radius: 50%;
}

//----------------

.form__title {
  margin: 0;
  font-weight: 400;
  text-transform: none;
  font-size: 24px;
  line-height: 24px;
}

.form__fieldset-wrapper {
  position: relative;
  padding: 0;
  margin: 0;
  // background-color: antiquewhite;
}

.form__fieldset-wrapper::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 15px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: $color-bg-green;
}

.form__fieldset--pet-owner,
.form__fieldset--comment,
.form__fieldset--optional {
  padding: 0;
  margin: 0 0 24px;
  border: none;
}

.form__fieldset--optional {
  margin: 0 0 34px;
}

.form__fieldset--pet-owner .form__title {
  width: 190px;
  margin: 0 0 35px;
  padding: 0 20px;
  background-color: $color-white;
}

.form__fieldset--comment .form__title {
  margin: 0 0 38px;
  padding: 0 20px;
  background-color: $color-white;
}

.form__fieldset--optional .form__title {
  margin: 0 0 34px;
  padding: 0 20px;
  background-color: $color-white;
}

.form__textarea {
  display: flex;
  padding: 0 20px;
}

.form__textarea-text {
  flex-grow: 1;
  padding: 14px 15px;
  height: 150px;
  border: 1px solid $color-line-gray;
  text-align: center;
}

.form__fieldset--pet-owner .form__items {
  list-style-type: none;
  margin: 0;
  padding: 0 20px;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 45px 45px;
  row-gap: 14px;
}

.form__fieldset--optional .form__items {
  list-style-type: none;
  margin: 0;
  padding: 0 20px;

  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 30px);
  row-gap: 13px;
  align-items: center;
}

.form__label-checkbox {
  position: relative;
  padding-left: 40px;
}

.form__fieldset--optional .form__label-checkbox::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 30px;
  height: 30px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  border: 1px solid $color-line-gray;
}

.form__fieldset--optional .form__label-checkbox::after {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 26px;
  height: 26px;
  top: 50%;
  left: 2px;
  transform: translateY(-50%);
  background-image: url("../../img/tick.svg");
  border-radius: 50%;
}

//------------------------------

.form__fieldset--pet-owner .form__item {
  display: flex;
  align-items: center;
}

.form__fieldset--pet-owner .form__label {
  padding-right: 13px;
}

.form__fieldset--pet-owner .form__input {
  padding: 0 13px;
  align-self: stretch;
  flex-grow: 1;
  border: 1px solid $color-line-gray;
}

// ------------------------

.form__button-wrapper {
  padding: 0 20px;
  margin-bottom: 32px;
  display: grid;
  grid-template-rows: 40px;
  row-gap: 21px;
}

.form__button {
  background-color: $color-bg-green;
  border: none;
  outline: none;
  color: $color-white;
}

.form__footnote {
  margin: 0;
  font-family: $font-text;
  text-transform: none;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
}

