  .form__button-wrapper {
    padding: 0 70px;
    margin-bottom: 72px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 52px;
    column-gap: 64px;
    row-gap: 0;
  }

  .form__button {
    background-color: $color-bg-green;
